/** Bootstrap bug : the bg appears over the modal. Annoying. */
.modal-backdrop{
    display: none;
    visibility: hidden;
}

.modal-bg{
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
}

.hide{
    display: none;
}

.show{
    display: block;
}

.cursor-pointer{
  cursor: default!important;
}
