.famille-title{
    min-height: 50px;
}

.famille-title h4{
    font-size: .9em;
    line-height: 1.3em;
}

.bg-round-grey{
    background-color: rgb(235, 235, 235);
    border-bottom-left-radius: .25rem;
    border-bottom-right-radius: .25rem;
}
table .product-desc{
}

table .product-sous-famille{
    font-weight: bold;
    font-size: .9em;
}

table .product-caracteristiques{
    font-size: .8em;
    max-width: 300px;
}

table .product-ref{
    font-weight: bold;
}

table .va-middle{
    vertical-align: middle;
    text-align: center;
}

table .product-quantity-input{
    max-width: 60px;
}

table .product-button{
    max-width: 150px;
    font-size: .9em;
    margin: 0 auto;
}

table .product-img{
    max-width: 120px;
    max-height: 120px;
}

table select{
    max-width: 200px;
}
.table-visible-blue{
  display: table-row;
  background-color: #B0D7F1;
}

.table-visible-green{
  display: table-row;
  background-color: rgb(176, 241, 176);
}

.flip-ver{
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.collapse-button:hover{
  cursor: pointer;
}

.table-hidden{
  display: none;
}

.fa-check {
  font-size: 18px;
}

.d-t-inline{
  display: inline-block;
  margin-right: 10px;
  margin-left: 10px;
  height: 30px;
  vertical-align: top;
  padding: 0;
}

.cust-fa{
  display: block;
  height: 30px;
  line-height: 15px;
  border: 1px solid;
}

/** Bootstrap bug : the bg appears over the modal. Annoying. */
.modal-backdrop{
    display: none;
    visibility: hidden;
}

.modal-bg{
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
}

.hide{
    display: none;
}

.show{
    display: block;
}

.cursor-pointer{
  cursor: default!important;
}

.not-found{
    width: 100vw;
    text-align: center;
    vertical-align: middle;
    font-size: 10vh;
    font-family: "Arial";
    font-weight: bold;
    margin-top: 42vh;
}
